.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.project-review {
	position: relative;

	&__content {
		margin-left: -20px;
		margin-right: -20px;

		&__title {
			position: absolute;
			top: 24px;
			left: 20px;
		}

		&__item {
			display: flex;
			gap: 20px;
			padding-top: 24px;
			padding-bottom: 40px;

			@media (max-width: 567px) {
				flex-wrap: wrap;
				padding-left: 20px;
			}

			&.brief {
				@media (max-width: 567px) {
					padding-top: 60px;
				}
			}

			&__title {
				width: calc((100% - 20px) / 2);
				font-style: normal;
				font-weight: 400;
				font-size: 16px;
				line-height: 124%;

				@media (max-width: 567px) {
					width: 100%;
				}

				& span {
					margin-left: 60%;

					@media (max-width: 567px) {
						margin-left: 0;
					}
				}
			}

			&__text {
				width: calc((100% - 20px) / 2);
				font-style: normal;
				font-weight: 400;
				font-size: 16px;
				line-height: 124%;

				@media (max-width: 567px) {
					width: 100%;
				}

				&__wrap {
					width: 50%;

					@media (max-width: 999px) {
						width: 100%;
						padding-right: 20px;
					}
				}
			}
		}
	}
}

.thumbnail {
	// padding: 112px 0;
}

.section-filtr {
	display: flex;
	gap: 20px;
	justify-content: space-between;
	padding-top: 24px;
	padding-bottom: 56px;

	&__items {
		display: flex;
		gap: 8px;
		flex-wrap: wrap;

		&__button {
			gap: 8px;

			@media (max-width: 379px) {
				font-size: 10px;
			}

			&.site-btn.active {
				background: #2B2B2B;
				color: #F1EDE8;
				pointer-events: none;
			}
		}
	}

	&__buttons {
		display: flex;
		gap: 24px;

		&__item {
			text-transform: capitalize;
			color: #2B2B2B80;
			cursor: pointer;

			@media (max-width: 379px) {
				font-size: 14px;
			}

			&.active {
				color: #2B2B2B;
				text-decoration-line: underline;
				pointer-events: none;
			}
		}
	}
}

.section-projects {
	padding-bottom: 112px;
	display: none;

	& .featured__content {
		margin-top: 0;
	}

	& .extras__content {
		margin-top: 0;
	}

	&.active {
		display: block;
	}
}


.reviews .services__content {

	@media (min-width: 530px) {
		padding: 0;
	}
}

.slide-review {
	display: flex;
	flex-direction: column;
	box-sizing: border-box;
	padding: 32px;
	border: 1px solid #2B2B2B33;

	@media (min-width: 530px) {
		width: 400px;
		height: 540px;
	}

	&__img {
		display: flex;
		width: 88px;
		height: 88px;
		justify-content: center;
		align-items: center;
		border-radius: 360px;
		background: #2e383675;
		overflow: hidden;
		margin-bottom: 32px;

		// @media (max-width: 530px) {
		// 	margin-left: auto;
		// 	margin-right: auto;
		// }
	}

	&__text {
		font-size: 18px;
		line-height: 132%;

	}

	&__meta {
		margin-top: 60px;

		@media (min-width: 530px) {
			margin-top: auto;
		}

		&__name {
			display: block;
			font-size: 22px;
			font-weight: 500;
			line-height: 135%;

		}

		&__job {
			display: block;
			font-size: 16px;
			line-height: 124%;
			color: #2B2B2B80;

		}

	}
}

.project-blocks {

	&__item {
		margin-bottom: 112px;
		padding: 0;

		&__title {
			padding-left: 20px;
			padding-right: 20px;
			padding-bottom: 56px;
		}

		&__content {

			&__text {
				display: flex;
				gap: 20px;
				padding-top: 24px;
				padding-bottom: 40px;

				@media (max-width: 567px) {
					flex-wrap: wrap;
				}

				&__title {
					width: calc((100% - 20px) / 2);
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 124%;

					@media (max-width: 567px) {
						width: 100%;
					}

					& span {
						margin-left: 0;
					}
				}

				&__text {
					width: calc((100% - 20px) / 2);
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 124%;

					@media (max-width: 567px) {
						width: 100%;
					}

					&__wrap {
						width: 50%;

						@media (max-width: 999px) {
							width: 100%;
						}
					}
				}
			}

			&__gallery {
				display: flex;
				flex-wrap: wrap;
				gap: 20px;
				margin-top: 40px;

				& img {
					width: calc((100% - 20px) / 2);

					@media (max-width: 469px) {
						width: 100%;
					}
				}
			}
		}
	}
}

.page .project-blocks {
	padding-top: 40px;
}
