a {
	color: $color__link;

	// &:visited {
	// 	color: $color__link-visited;
	// }

	&:hover,
	&:focus,
	&:active {
		outline: none;
		color: $color__link-hover;
	}

	&:focus {
		outline: none;
	}

	&:hover,
	&:active {
		outline: 0;
	}

	&.site-color {
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 124%;
		color: $color__text-main;

		&:hover {
			color: $color__text-main;
			text-decoration: none;

		}
	}
}
