.featured {

    &__content {
        display: flex;
        gap: 20px;
        flex-wrap: wrap;
        margin-top: 56px;

        &__card {
            position: relative;
            width: calc((100% - 20px) / 2);

            @media (max-width: 399px) {
                width: 100%;
            }

            &:nth-child(n+3) {
                margin-top: 26px;

                @media (max-width: 399px) {
                    margin-top: 0;
                }
            }

            &__link {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                z-index: 9;
            }

            &__img {
                display: flex;
                transition-timing-function: cubic-bezier(0.42, 0, 0.58, 1.0);
                transition-duration: .5s;
                transition-property: transform, opacity;

                &.front {
                    transform: rotateY(0deg);
                }

                &.back {
                    position: absolute;
                    opacity: 0;
                    top: 0px;
                    left: 0px;
                    width: 100%;
                    height: auto;
                    transform: rotateY(-180deg);
                }
            }

            &__title {
                font-style: normal;
                font-weight: 500;
                font-size: 22px;
                line-height: 135%;
                margin-top: 12px;

                & a {
                    color: #2B2B2B;
                    text-decoration: none;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

            &__text {
                margin-top: 4px;

                & p {
                    margin: 0;
                }
            }

            &-number1 {
                width: calc((100% - 20px) / 2);

                @media (max-width: 767px) {
                    width: 100%;
                }
            }

            &-number4 {
                width: calc((100% - 20px) / 2);
                margin-left: auto;

                @media (max-width: 767px) {
                    width: 100%;
                }
            }

            &-number1,
            &-number2,
            &-number3,
            &-number4,
            &-number5,
            &-number6 {
                cursor: pointer;
                position: relative;

                &:hover .featured__content__card__title a {
                    text-decoration: underline;
                }

                &:hover .featured__content__card__img.front {
                    transform: rotateY(180deg);
                }

                &:hover .featured__content__card__img.back {
                    opacity: 1;
                    transform: rotateY(0deg);
                }
            }
        }
    }
}

.extras {

    &__content {
        margin-top: 56px;

        &__list {
            position: relative;
            display: flex;
            gap: 20px;
            margin-left: -20px;
            margin-right: -20px;
            padding: 24px 20px;
            border-bottom: 1px solid rgba(43, 43, 43, 0.2);

            @media (max-width: 799px) {
                flex-wrap: wrap;
            }

            &__link {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 99;
            }

            &__title {
                min-width: 32%;
                font-style: normal;
                font-weight: 500;
                font-size: 22px;
                line-height: 135%;

                @media (max-width: 799px) {
                    width: 100%;
                }
            }

            &__industry {
                display: flex;
                align-items: center;
                min-width: 12%;
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 124%;
            }

            &__text {
                display: flex;
                align-items: center;
                min-width: 240px;
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 124%;

                & p {
                    margin: 0;
                }
            }

            &__img {
                position: absolute;
                right: 80px;
                top: -180px;
                visibility: hidden;
                width: 0;
                height: 0;

                @media (max-width: 799px) {
                    display: none;
                }
            }

            &:hover {
                background: #2B2B2B;
                color: #F1EDE8;

                &.extras__content__list__title {
                    text-decoration: underline;
                }
            }

            &:hover .extras__content__list__title {
                text-decoration: underline;
            }

            &:hover .extras__content__list__img {
                visibility: visible;
                width: 335px;
                height: 418px;
                z-index: 9;
            }
        }
    }
}
